<template>
  <div>
    <div class="main-content__title"></div>
    <el-form label-width="100px">
      <template v-for="(obj ,index) in $dc.projectPathArray">
        <el-form-item label="">
          <el-input v-model="obj.key" placeholder="" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="obj.value" placeholder="" />
        </el-form-item>
        <el-row :gutter="10" style="margin-top: 5px;">
          <el-col :span="2">
            <el-button
              type="danger"
              icon='delete'
              size="mini"
              @click='deleteParam(obj,index,projectPathArray)'
            />
          </el-col>
        </el-row>
      </template>
      
      <el-form-item>
        <el-button @click="addParam"></el-button>
        <el-button type="primary" @click="saveFile"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import profileApi from '../../../api/profile';
import forEach from 'lodash/forEach';

export default {
  name: 'project-path',

  methods: {
    deleteParam(row, index, list) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$dc.projectPathArray.splice(index, 1);
      });
    },

    async saveFile() {
      var projectPathMap = {};
      forEach(this.$dc.projectPathArray, obj => {
        projectPathMap[obj.key] = obj.value;
      });
      this.$dc.profile.projectPath = projectPathMap;
      let response = await profileApi.saveFile(this.$dc.profile);
      let serverData = response.data;
      if (serverData.code == 0) {
        this.$message({
          type: 'success',
          message: '!'
        });
      } else {
        this.$message.error(`${serverData.msg}`);
      }
    },

    addParam() {
      this.$dc.projectPathArray.push({
        key: '',
        value: ''
      });
    }
  }
};
</script>



